import React from 'react';
import AppRoutes from './AppRoutes.js';
import './App.css';
import { MsalAuthenticationTemplate, MsalProvider } from '@azure/msal-react';
import { InteractionType } from '@azure/msal-browser';
import { msalInstance } from './MsalConfig';
import useSessionId from './components/useSessionId';
let implementEsoLogin = false;
if (window.location.origin.indexOf('genai-agent-training') >= 0) {
  implementEsoLogin = true;
} else {
  implementEsoLogin = false;
}

function ErrorComponent(error) {
  return (
    <h2 style={{ textAlign: 'left', color: 'red' }}>
      Error: Something went wrong. Please try again after some time: {error}
    </h2>
  );
}

function LoadingComponent() {
  return <h2 style={{ textAlign: 'left' }}>Authenticating...</h2>;
}
const loginRequest = { scopes: ['user.read'] };

function App() {
  return !implementEsoLogin ? (
    <AppRoutes />
  ) : (
    <MsalProvider instance={msalInstance}>
      <MsalAuthenticationTemplate
        interactionType={InteractionType.Redirect}
        authenticationRequest={loginRequest}
        errorComponent={ErrorComponent}
        loadingComponent={LoadingComponent}
      >
        <AppRoutes sessionId={useSessionId} />
      </MsalAuthenticationTemplate>
    </MsalProvider>
  );
}

export default App;
