import { useState, useEffect} from 'react';
import {v4 as uuidv4} from 'uuid';

 const useSessionId = () => {
    const [sessionId, setSessionId] = useState(null);

useEffect(()=> {
const generateNewSessionId = () => {
    const newSessionId = uuidv4();
    sessionStorage.setItem('sessionId', newSessionId);
    setSessionId(newSessionId);
};
const existingSessionId = sessionStorage.getItem('sessionId');
if(!existingSessionId){
    generateNewSessionId();
} else {
    setSessionId(existingSessionId);
}

const handleBeforeUnload = () => {
    generateNewSessionId();
};
window.addEventListener('beforeunload', handleBeforeUnload);
return () => {
    window.removeEventListener('beforeunload', handleBeforeUnload);
};
},[]);

    // useEffect(()=> {
    //     const storedTimeStamp = sessionStorage.getItem('sessionTimestamp');
    //     const storedSessionId = sessionStorage.getItem('sessionId');
    //     const currentTime = new Date().getTime();
    //     const sessionTimeout = 60*60*1000;
    //     if(storedTimeStamp && currentTime -storedTimeStamp < sessionTimeout && storedSessionId){
    //        setSessionId(storedSessionId);
    //     }else {
    //         const newSessionId = uuidv4();
    //         setSessionId(newSessionId);
    //         sessionStorage.setItem('sessionId', newSessionId);
    //         sessionStorage.setItem('sessionTimestamp', currentTime);
    //         console.log('NEW SESSION ID', existingSessionId);
    //     }
    // },[]);
    return sessionId;
};
export default useSessionId;