import React, {useState} from 'react';
import {Button, Grid} from '@mui/material';

const suggestions = [
    'Upload an Image', 
    'PickYZ Uploader', 
    // 'PickYZ Assisant', 
    // 'Policy related question'
];
const suggestionsAftetMessage =[
    'Upload an Image',
    'PickYZ Uploader',
];
const Suggestions = (props) => {
    const [uploadClick, setUploadClick] = useState(false);
    const handleClickBtn = (suggestion) => {
        console.log('SUGGESTION', suggestion);
        setUploadClick(true);
        if(suggestion==='Upload an Image'||suggestion==='PickYZ Uploader'){
            console.log('CAme to loop');
            props.setSelectedOption(suggestion);
            props.onUploadClick();
        }
    };
    return (
        <Grid container spacing={2} justifyContent='center'>
            {props.isMessageAvailable === true || uploadClick === true ? suggestionsAftetMessage.map((suggestion,index)=> (
                <Grid item key={index}>
                    <Button style={{height: '100px',width: '400px',borderRadius: '24px'}} sx={{textTransform:'none', fontSize:'20px', color:'#4D8836',borderColor:'#4D8836'}}
                    variant='outlined' onClick={()=> handleClickBtn(suggestion)}>
                        <span>{suggestion}</span></Button>
                    </Grid>
            )):suggestions.map((suggestion,index)=> (
                <Grid item key={index}>
                    <Button style={{height: '100px',width: '400px',borderRadius: '24px', color:'#4D8836', outlineColor:'#4D8836'}}
                    variant='outlined' onClick={()=> handleClickBtn(suggestion)} sx={{textTransform:'none', fontSize:'20px', borderColor:'#4D8836'}}>
                        <span>{suggestion}</span></Button>
                    </Grid>
            ))}
        </Grid>
    );
};

export default Suggestions;