import React from 'react';
import { BrowserRouter, Link, Route, Routes } from 'react-router-dom';
import HomePage from '../src/components/HomePage/Chatbot.js';
function AppRoutes(props) {
  return (
    <BrowserRouter>
      <Routes>
        <Route path='/*' element={<HomePage sessionId={props.sessionId}/>}></Route>
      </Routes>
    </BrowserRouter>
  );
}

export default AppRoutes;
