import React, { useState, useRef, useEffect } from 'react';
import { AppBar, Toolbar, IconButton, TextField, Button, Container, Grid, Paper, List, ListItem, ListItemText, Avatar, Dialog, DialogTitle, DialogContent, DialogActions, Typography } from '@mui/material';
import AttachFileIcon from '@mui/icons-material/AttachFile';
import SendIcon from '@mui/icons-material/Send';
import './Chatbot.css';
import AccountCircle from '@mui/icons-material/AccountCircle';
import Suggestions from './Suggestions';
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';
import NotificationsActiveIcon from '@mui/icons-material/NotificationsActive';
import BeYouIcon from '../../images/pycwyz_avatar.jpg';  // here
import ConsumeWiselogo from '../../images/pycwyz.gif';  // here
import NurseIcon from '../../images/user.png';   // here
import BeYouBanner from '../../images/BeYou-Banner.png';
import MicOffIcon from '@mui/icons-material/MicOff';
import MicIcon from '@mui/icons-material/Mic';
import Header from './Header';
import beyougif from '../../images/BeYou-GIF.gif';   // here
import beyougif1 from '../../images/BeYou-GIF1.gif';
import { setSourceMapsEnabled } from 'process';
import DoneIcon from '@mui/icons-material/Done';
import DoneAllIcon from '@mui/icons-material/DoneAll';
import CloseIcon from '@mui/icons-material/Close';
const Chatbot = (props) => {
  const startListening = () => SpeechRecognition.startListening({ continuous: true });
  const stopListening = () => SpeechRecognition.stopListening({ continuous: false });
  const { transcript, resetTranscript, browserSupportsSpeechRecognition, listening } = useSpeechRecognition();
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [file, setFile] = useState(null);
  const fileInputRef = useRef(null);
  const [fileBase64, setFileBase64] = useState(null);
  const [selectedOption, setSelectedOption] = useState('');
  const [messagesAvailable, setMessagesAvailable] = useState(false);
  const [shouldVibrate, setShouldVibrate] = useState(false);
  const [open,setOpen] = useState(false);
  const [alarmVibrationStopped, setAlarmVibrationStopped] = useState(false);
  const [approveClicked, setApproveClicked] = useState(false);
  const [nurseBotMessage, setNurseBotMessage] = useState([]);
  useEffect(() => {
    if (!browserSupportsSpeechRecognition) {
      return <span>Browser doesn't support speech recognition.</span>;
    }
  },[browserSupportsSpeechRecognition]);
  const escapeHtml = (unsafe) => {
    return unsafe.replace(/&/g,'&amp;').replace(/</g,'&lt;').replace(/>/g,'gt;').replace(/"/g,'&quot;').replace(/'/g,'&#039;');
  };
  const formatText = (text) =>{
  let safeText = escapeHtml(text);
  safeText = safeText.replace(/\*\*(.*?)\*\*/g,'<br/><b>$1</b>');
  safeText = safeText.replace(/\*\s/g,'<br/>');
  return {__html: safeText};
  };
  useEffect(()=> {
    setInput(transcript);
  },[transcript]);

  const toggleListening = () => {
    if(listening){
      SpeechRecognition.stopListening();
    } else {
      SpeechRecognition.startListening({continuous: false});
    }
  };
  const handleKeyPress = (e) => {
    if(e.key==='Enter' && !e.shiftKey){
      e.preventDefault();
      handleSendMessage();
    }
  };
  useEffect(()=> {
    let timer;
    if(messages.some(msg=>msg.text.includes('202') && msg.sender==='user') && alarmVibrationStopped===false){
      timer = setTimeout(()=> {
        setShouldVibrate(true);
        setAlarmVibrationStopped(true);
      },15000);
    }
    return () => clearTimeout(timer);
  },[messages]);
  const handleSendMessage = async () => {
    if (input.trim() || file) {
      console.log(fileBase64);
      const userMessage = { text: input, sender: 'user' };
      setMessagesAvailable(true);
      setMessages([...messages, userMessage]);
      setInput('');
      setFile(null);
      try{
        if(selectedOption === 'Upload an Image'){
          const response = await fetch('https://consumewise-447533584464.us-central1.run.app/notes',{
            method:'POST',
            headers:{
              'Content-Type':'application/json',
            },
            body:JSON.stringify({query:input}),
          });
          const data = await response.json();
          console.log('DATA', data);
          const botMessage = {text: data.response, sender:'bot', option:'image'};
          //setNurseBotMessage((prevMessages)=>[...prevMessages,botMessage]);
          setMessages((prevMessages)=>[...prevMessages, botMessage]);
      } else {
        const response = await fetch('https://beyou-innovation-gw-5128wcnr.uc.gateway.dev/response',{
          method:'POST',
          headers:{
            'Content-Type':'application/json',
          },
          body:JSON.stringify({session_id:props.sessionId, query:input}),
        });
        const data = await response.json();
        console.log('DATA', data);
        const botMessage = {text: data.response, sender:'bot', option:'chatbot'};
        setMessages((prevMessages)=>[...prevMessages, botMessage]);
      }
    
      } catch(error){
        console.error('Error sending message:', error);
      }
    }
  };

  const handleFilesChange = (files) => {
    const selectedFile = files[0];
    setFile(selectedFile);
    console.log('SELECTEDFile', selectedFile);
    if(selectedFile){
    
      const reader = new FileReader();
      reader.readAsDataURL(selectedFile);
      reader.onload= async () => {
        const base64String = reader.result;
        console.log('BASE^$SET', base64String);
        const userMessage = { text:selectedOption === 'Upload an Image'?'Image uploaded':'Nurse Handwritten Notes Uploaded', sender: 'user',  
          file:selectedOption === 'Upload an Image' || selectedOption === 'Nurse Handwritten Notes Uploader'?
          base64String:'' };
        setMessages([...messages, userMessage]);
        setFileBase64(base64String);
        try{
          const response = await fetch( selectedOption === 'Upload an Image'?
            'https://consumewise-447533584464.us-central1.run.app/notes':
            'https://consumewise-447533584464.us-central1.run.app/notes',{
            method:'POST',
            headers:{
              'Content-Type':'application/json',
            },
            body: JSON.stringify({image:base64String}),
          });
          const data = await response.json();
          console.log('DATA', data.results);
          const botMessage = {text: data.results, sender:'bot', option:selectedOption};
          setMessages((prevMessages)=>[...prevMessages, botMessage]);
        }catch(error){
          console.error('Error Uploading file:',error);
        }
      };
      
    }
    if(fileInputRef.current){
      fileInputRef.current.value = '';
    }
  };

  const handleFilesError = (error) => {
    console.error('error code ' + error.code + ': ' + error.message);
  };
  const handleApprove = () => {
    setApproveClicked(true);
  };
const handleUploadClick = () => {
  console.log('HELLO');
  fileInputRef.current.click();
};
const handleBellClick = () => {
  setOpen(true);
  setShouldVibrate(false);
};
const handleClose = () => {
  setOpen(false);
};
console.log('nurseBotMessage', nurseBotMessage, 'messagess>>>', messages);
  return (
    <div>
       {/* <div style={{color:'#7d7d7d', fontSize:'30px', marginLeft:'20px', marginTop:'10px'}}>BeYou - Nurse Assistant</div> */}
        {/* <div style={{color:'#7d7d7d', fontSize:'30px',marginLeft:'-100px',marginTop:'-100px'}}><img src={BeYouIcon} style={{width:'700px', height:'400px'}}></img></div>   */}
        {/* <div style={{color:'#7d7d7d', fontSize:'30px', marginLeft:'-100px'}}><img src={ConsumeWiselogo} style={{width:'700px', height:'400px'}} /> */}
        <div style={{color:'#7d7d7d', fontSize:'30px', marginLeft:'-70px'}}><img src={ConsumeWiselogo} style={{width:'400px', height:'340px'}} />


</div>

      {/* <div style={{color:'#7d7d7d',  marginLeft:'20px'}}><img style={{width:'200px', height:'40px'}}src={BeYouBanner} /></div> */}
    <Container style={{ display: 'flex', overflow:'none', flexDirection: 'column', height: '50vh', justifyContent: 'space-between', backgroundColor: '#ffffff', padding: 0 }}>    
      <div style={{marginLeft:'100%', display:'flex', marginTop:'-1%'}}>
  
       {/* {shouldVibrate?
        <IconButton color='inherit' onClick={handleBellClick}>
        <NotificationsActiveIcon className={shouldVibrate?'vibrate':''} sx={{width:'50px',marginTop:'-500px',height:'50px', color:'#1976d2'}}/>
        <Typography style={{marginTop:'-500px'}} className={shouldVibrate?'vibrate':''}>ALERT</Typography>
        </IconButton>:  
        <NotificationsActiveIcon className={shouldVibrate?'vibrate':''} sx={{width:'50px',marginTop:'-270px', height:'50px', color:'#7d7d7d'}}/>} */}
        {/* <><AccountCircle sx={{height:'60px', width:'60px',color:'#1976d2',  marginLeft:shouldVibrate?'60px':'85px', marginTop:'-270px'}}/></> */}
        </div>
      {/* <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', marginTop: '3%' }}>
        <img src={BeYouIcon} alt="Logo" style={{ width: '150px', height: '150px', marginTop:messages.length>0?'-40%':'-3%'}} />
      </div> */}
      <br/><br/>
      {messages.length>0 && <div className="chatbot-scroll" 
      style={{ flexGrow: 1, width: '100%', overflowY: 'auto', boxSizing: 'border-box', marginTop:'-20%'}}>
        <List style={{height:'100%'}}>
          {messages.map((message, index) => (
            <ListItem key={index} style={{ display: 'flex', justifyContent: message.sender === 'user' ? 'flex-end' : 'flex-start', marginBottom: '8px' }}>
              {message.sender === 'bot'&&
               <Avatar src={BeYouIcon} style={{marginRight:'15px', width:'100px', height:'100px',}}/> 
              }
              <Paper style={{ padding: '8px 16px', borderRadius: '12px', maxWidth: '70%'}} elevation={3}>
                {message.file && <img style={{width:'400px', height:'300px'}} src={message.file}/>}
                <div dangerouslySetInnerHTML={formatText(message.text)} />
                {selectedOption === 'Nurse Handwritten Notes Uploader'&& approveClicked===false &&  
                message.sender === 'bot' && message.option==='Nurse Handwritten Notes Uploader' && 
                <><br /><div style={{ display: 'flex', gap: '10px' }}>
                    <Button variant='contained' sx={{
                      backgroundColor: 'white', borderColor: 'green', borderWidth: '2px',
                      borderStyle: 'solid', color: 'green', borderRadius: '20px'
                    }} onClick={handleApprove}><DoneIcon style={{ color: 'green' }} />Approve</Button>
                    <Button variant='contained' sx={{
                      backgroundColor: 'white', borderColor: 'red', borderWidth: '2px',
                      borderStyle: 'solid', color: 'red', borderRadius: '20px'
                    }}><CloseIcon style={{ color: 'red' }} /> Reject</Button></div></>
                  }
                  {selectedOption === 'Nurse Handwritten Notes Uploader'&& approveClicked===true && message.sender === 'bot' && message.option==='Nurse Handwritten Notes Uploader' &&
                     <><br /><div style={{ display: 'flex', gap: '75%' }}><Button variant='contained' sx={{
                    backgroundColor: 'green', borderColor: 'white', borderWidth: '2px',
                    borderStyle: 'solid', color: 'white', borderRadius: '20px'
                  }}><DoneIcon style={{ color: 'white' }} />Approved</Button><DoneAllIcon /></div></>
                  }
              </Paper>
              {message.sender==='user'&& 
               <Avatar src={NurseIcon} style={{marginRight:'10px',  width:'60px', height:'60px', marginLeft:'10px'}}/>}
            </ListItem>
          ))}
        </List>
      </div>}
      <br/><br/>
      <div style={{ display: 'flex', justifyContent: 'center', marginBottom:messages.length>0?'6%':'7%' }}>
        <Suggestions onUploadClick={handleUploadClick} setSelectedOption={setSelectedOption} isMessageAvailable={messagesAvailable}/>
      </div>
    
      <AppBar position="fixed" color="transparent" style={{  marginTop: '41%',width: '100%', bottom: 0, backgroundColor: '#ffffff', padding: '8px', boxShadow: '0 -1px 5px rgba(0,0,0,0.1)' }}>
        <Toolbar>
          <input type='file' ref={fileInputRef} style={{display:'none'}} onChange={(e)=>handleFilesChange(e.target.files)}/>
          {/* <ReactFiles
            className='files-dropzone'
            onChange={handleFilesChange}
            ref={fileInputRef}
            onError={handleFilesError}
            accepts={['image/*', '.pdf']}
            multiple={false}
            maxFiles={1}
            maxFileSize={10000000}
            minFileSize={0}
            clickable
          > */}
            <IconButton color="inherit" onClick={()=> fileInputRef.current.click()}>
              <AttachFileIcon style={{marginLeft:'200px'}}/>
            </IconButton>
            <IconButton color='inherit' onClick={toggleListening}>
              {listening?<MicOffIcon />:<MicIcon />}
            </IconButton>
          {/* </ReactFiles> */}
          <TextField
            InputProps={{}}
            onKeyDown={handleKeyPress}
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder="Type a message..."
            multiline
            rows={1}
            sx={{
              '& fieldset':{border:'none'}
            }}
            style={{width: '70%', backgroundColor: '#f1f1f1', borderRadius: '24px', padding: '8px 16px', marginRight:'20px' }}
          />
          <Button
            
            variant="contained"
            endIcon={<SendIcon />}
            onClick={handleSendMessage}
            style={{ marginLeft: '8px', backgroundColor:'#4D8836' }}
          >
            Send
          </Button>
        </Toolbar>
        <div style={{marginLeft:'35%'}}>@2024 Copyright. All rights reserved. Confidential. For internal use only. </div>
      </AppBar>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle><b>Notification</b></DialogTitle>
        <DialogContent style={{width:'300px'}}>
          <p><b>Patient Name: </b>Paul C</p>
          <p><b>Room No: </b>202</p>
          <p><b>Dosage: </b></p>
          <p>Lisinopril 10mg - 1</p>
          <p>Acetaminophen 500mg</p>
          <p>Dicyclomine 20mg</p>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color='primary'>
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
    </div>
  );
};

export default Chatbot;