import { LogLevel, PublicClientApplication, EventType, EventMessage, AuthenticationResult } from '@azure/msal-browser';
 
let clidntID='934a5ac3-cf5e-4e4e-b275-2bc45eec10dd';
if(window.location.origin.indexOf('qa.ahap')>=0){
  clidntID='a20201e1-7605-4927-ae7c-d3a85355d866';
}
const redirectURL=()=>{
  if(window.location.host.indexOf('localhost')>=0){
    return 'https://genai-agent-training-qa.ahap.accentureanalytics.com/';
  }else{
    return  window.location.origin;
  }
};

export const MsalConfig = {
    auth: {
      clientId: clidntID,
      tenantId: 'f3211d0e-125b-42c3-86db-322b19a65a22',
      authority:
        'https://login.microsoftonline.com/f3211d0e-125b-42c3-86db-322b19a65a22',
      redirectUri: redirectURL(),
    },
    cache: {
        cacheLocation: 'sessionStorage', // This configures where your cache will be stored
        storeAuthStateInCookie: false, // Set this to 'true' if you are having issues on IE11 or Edge
    },
    system: {
      loggerOptions: {
        loggerCallback: (level, message, containsPii) => {
          if (containsPii) {
            return;
          }
          switch (level) {
            case LogLevel.Error:
              console.error(message);
              return;
            case LogLevel.Info:
              console.info(message);
              return;
            case LogLevel.Verbose:
              console.debug(message);
              return;
            case LogLevel.Warning:
              console.warn(message);
              return;
            default:
              return;
          }
        },
      },
    },
  };

// MSAL should be instantiated outside of the component tree to prevent it from being re-instantiated on // re-renders.
export const  msalInstance = new PublicClientApplication (MsalConfig);
 
 

// Default to using the first account if no account is active on page load
// if (!msalInstance.getActiveAccount() && msalInstance.getAllAccounts().length > 0) {
//     // Account selection logic is app dependent. Adjust as needed for different use cases. 
//     msalInstance.setActiveAccount(msalInstance.getAllAccounts()[0] );
    
// }

// Listen for sign-in event and set active account
msalInstance.addEventCallback((event) => {
    const payload = event.payload ;
    if (event && event.eventType === EventType.LOGIN_SUCCESS && payload.account) {
       
        const account = payload.account;
        msalInstance.setActiveAccount(account);
     
}
});

 